import './App.css';
import Navbar from './components/Navbar';
import PokedexBody from './components/PokedexBody';
import { useEffect, useState } from 'react';
import { getPokemonData, getPokemonList, getNextPage, getPrevPage } from './api';
import { Button, Typography } from '@mui/material';

function App() {
  const [pokemonList, setPokemonList] = useState([]);
  const [nextPage, setNextPage] = useState();
  const [prevPage, setPrevPage] = useState();
  const [total, setTotal] = useState();
  const [loading, setLoading] = useState(true);

  const fetchPokemonList = async () => {
    try {
      const data = await getPokemonList();
      const promises = data.results.map(async (pokemon) => {
        return await getPokemonData(pokemon.url)
      })
      const results = await Promise.all(promises)
      setPokemonList(results)
      setLoading(false)
      setNextPage(data.next)
      setPrevPage(data.previous)
    } catch (err) { }
  }

  const fetchNextPage = async () => {
    try {
      const data = await getNextPage(nextPage);
      const promises = data.results.map(async (pokemon) => {
        return await getPokemonData(pokemon.url)
      })
      const results = await Promise.all(promises)
      setPokemonList(results)
      setLoading(false)
      setNextPage(data.next)
      setPrevPage(data.previous)
    } catch (err) { }
  }
  const fetchPrevPage = async () => {
    try {
      const data = await getPrevPage(prevPage);
      const promises = data.results.map(async (pokemon) => {
        return await getPokemonData(pokemon.url)
      })
      const results = await Promise.all(promises)
      setPokemonList(results)
      setLoading(false)
      setNextPage(data.next)
      setPrevPage(data.previous)
    } catch (err) { }
  }

  useEffect(() => {

    fetchPokemonList()
  }, [])

  // const fetchSelectedPokemon = async () => {
  //   try {

  //   }
  // }

  return (
    <>

      <Navbar />
      <div>
        <Button
          onClick={() => { setLoading(true); fetchPrevPage() }}>prev</Button>
        <Button
          onClick={() => { setLoading(true); fetchNextPage() }}
          setLoading={true}>next</Button>
      </div>
      {loading ?
        <Typography variant='h4'>Cargando Pokédex...</Typography>
        :
        <PokedexBody pokemonList={pokemonList} />
      }
      <div>
        <Button
          onClick={fetchPrevPage}>prev</Button>
        <Button
          onClick={fetchNextPage}>next</Button>
      </div>

    </>
  );
}

export default App;
