import React, { useState } from 'react';
import { styled, alpha } from '@mui/material/styles';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { Button, CardActionArea, Grid, Paper } from '@mui/material';


// import * as React from 'react';
// import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';



const Search = styled('div')(({ theme }) => ({
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.common.white, 0.15),
    '&:hover': {
        backgroundColor: alpha(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
        marginLeft: theme.spacing(1),
        width: 'auto'
    },
}));


const pokemonCard = styled('div')(({ theme }) => ({
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'SpaceArround',
}));


const capitalizeWords = (str) => {
    return str
        .toLowerCase()
        .split(' ')
        .map((word) => word.charAt(0).toUpperCase() + word.slice(1))
        .join(' ');
};

export default function PokedexBody(props) {
    const { pokemonList } = props

    const [selectedCard, setSelectedCard] = useState(null)

    // ------------Dialog--------------------
    const [open, setOpen] = React.useState(false);
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('md'));

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };
    // ------------Dialog--------------------

    return (
        <>
            <Button>tryme</Button>
            <div
                padding={2}>
                <Grid
                    container
                    spacing={2}
                    alignItems="center"
                    sx={{ justifyContent: 'center' }}
                >
                    {pokemonList.map((pokemon, idx) => {
                        const handleCardSelection = () => {
                            setSelectedCard(pokemon.name)
                        }

                        return (
                            <>
                                <Grid
                                    item xs={12} sm={3} md={2}
                                    sx={{ mx: 1 }}  >
                                    <pokemonCard
                                        onClick={handleClickOpen}
                                        key={pokemon.name}
                                        sx={{ maxWidth: 500 }}>
                                        <Paper>
                                            <CardActionArea>
                                                <Button onClick={handleCardSelection}>SetSelectedCard</Button>
                                                <CardMedia
                                                    component="img"
                                                    height="auto"
                                                    image={pokemon.sprites.other.home.front_default ? pokemon.sprites.other.home.front_default : pokemon.sprites.front_default}
                                                    alt={pokemon.name} />
                                                <CardContent>
                                                    <Typography
                                                        gutterBottom variant="h5"
                                                        component="div">
                                                        {capitalizeWords(pokemon.name)}
                                                    </Typography>
                                                    <Typography variant="body2" color="text.secondary">
                                                        Pokédex: #{pokemon.id}
                                                    </Typography>
                                                </CardContent>
                                            </CardActionArea>
                                        </Paper>
                                    </pokemonCard>
                                </Grid>



                            </>
                        )
                    })}
                </Grid>
            </div>

            {/* -----------------------Dialog-------------------- */}

            <div>

                <Dialog
                    fullScreen={fullScreen}
                    open={open}
                    onClose={handleClose}
                    aria-labelledby="responsive-dialog-title"
                >
                    <DialogTitle id="responsive-dialog-title">
                        {"Pokemon Card"}
                    </DialogTitle>
                    {/* <DialogContent>
        <Paper>
            <CardActionArea>
                <Button onClick={handleCardSelection}>SetSelectedCard</Button>
                <CardMedia
                    component="img"
                    height="auto"
                    image={pokemon.sprites.other.home.front_default ? pokemon.sprites.other.home.front_default : pokemon.sprites.front_default}
                    alt={pokemon.name} />
                <CardContent>
                    <Typography
                        gutterBottom variant="h5"
                        component="div">
                        {capitalizeWords(pokemon.name)}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                        Pokédex: #{pokemon.id}
                    </Typography>
                </CardContent>
            </CardActionArea>
        </Paper>
    </DialogContent> */}
                    <DialogActions>
                        <Button autoFocus onClick={handleClose}>
                            Disagree
                        </Button>
                        <Button onClick={handleClose} autoFocus>
                            Agree
                        </Button>
                    </DialogActions>
                </Dialog>
            </div>

        </>
    )
}
